import { useState, useEffect } from "react";
import axios from "axios";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.js';
import 'bootstrap/dist/css/bootstrap.min.css';

function InvestorProfile() {
  const [investors, setInvestors] = useState([]);
  const [loading, setLoading] = useState(true);

  // Fetching investors data from backend API
  useEffect(() => {
    const fetchInvestors = async () => {
      try {
        const response = await axios.get("http://localhost:8080/investors");
        console.log("Investors fetched:", response.data); // Log the fetched data
        setInvestors(response.data.investors);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching investors:", error);
        setLoading(false);
      }
    };

    fetchInvestors();
  }, []);

  // Show loading message or error if data is not fetched
  if (loading) {
    return <div>Loading...</div>; // Or use a loading spinner
  }

  if (investors.length === 0) {
    return <div>No investors available</div>;
  }

  return (
    <Box sx={{ height: "50%", marginLeft: "30%", marginRight: "30%", marginTop: "5%" }}>
      <Grid container spacing={3}>
        {investors.map((investor, index) => (
          <Grid item xs={12} sm={6} md={4} lg={3} key={index}>
            <Card sx={{ maxWidth: "auto", maxHeight: "auto" }}>
              <CardMedia
                component="img"
                height="200"
                image={investor.photo ? `http://localhost:8080/${investor.photo[0]}` : "/default_image.jpg"}
                alt="Investor's Photo"
                sx={{ border: 1, borderColor: "primary.main" }}
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  {investor.name}
                </Typography>
                <Typography variant="body2"><b>Email:</b> {investor.email}</Typography>
                <Typography variant="body2"><b>Contact No:</b> {investor.contactNo}</Typography>
                <Typography variant="body2"><b>Field:</b> {investor.field}</Typography>
                <Typography variant="body2"><b>Total Investment till date:</b> {investor.totalInvestment}</Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      <div>
        <table className="table table-primary table-striped table-responsive table-hover">
          <thead>
            <tr>
              <th>Company Name</th>
              <th>Investment in CAD</th>
            </tr>
          </thead>
          <tbody>
            {investors.map((investor, index) =>
              investor.investments.map((investment, subIndex) => (
                <tr key={`${index}-${subIndex}`}>
                  <td>{investment.companyName}</td>
                  <td>{investment.amount}</td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </Box>
  );
}

export default InvestorProfile;
