import React, { useState, useEffect } from "react";
import axios from "axios";
import { Box, Grid, TextField, Button, Typography, Card, CardContent, Divider } from "@mui/material";

function StartupPage() {
  const [formData, setFormData] = useState({
    name: "",
    photo: [],
    website: "",
    description: "",
    industries: "",
    locations: "",
    amountRaised: "",
    fundingDuration: "",
  });

  const [message, setMessage] = useState("");
  const [selectedStartupId, setSelectedStartupId] = useState(null);
  const [startupData, setStartupData] = useState(null);
  const [startupList, setStartupList] = useState([]);  // Initialize as empty array

  // Fetch all startups from the backend
  useEffect(() => {
    axios
      .get("http://localhost:8080/startups/fetchAll")
      .then((response) => {
        setStartupList(response.data.startup || []);  // Safeguard in case of undefined
      })
      .catch((error) => {
        console.error("Error fetching startups:", error);
      });
  }, []);

  // Fetch selected startup details
  useEffect(() => {
    if (selectedStartupId) {
      axios
        .get(`http://localhost:8080/startups/${selectedStartupId}`)
        .then((response) => {
          setStartupData(response.data.startup);
        })
        .catch((error) => console.error("Error fetching startup details:", error));
    }
  }, [selectedStartupId]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    axios
      .post("http://localhost:8080/startups/add", formData)
      .then((response) => {
        setMessage("Startup added successfully!");
        setFormData({
          name: "",
          photo: [],
          website: "",
          description: "",
          industries: "",
          locations: "",
          amountRaised: "",
          fundingDuration: "",
        });
      })
      .catch((error) => {
        setMessage("Error adding startup!");
        console.error("Error:", error);
      });
  };

  return (
    <Box sx={{ padding: 4 }}>
      <Typography variant="h4" gutterBottom sx={{ color: 'brown' }}>
        Startup Management
      </Typography>

      <Grid container spacing={2}>
        {/* Left Column: Startup Profile */}
        <Grid item xs={12} md={6}>
          <Typography variant="h5" gutterBottom sx={{ color: 'brown' }}>
            Startup Details
          </Typography>
          <Divider sx={{ mb: 2 }} />
          {startupData ? (
            <Card>
              <CardContent>
                <Typography variant="h6">{startupData.name}</Typography>
                <Typography variant="body1">{startupData.description}</Typography>
                <Typography variant="body1">Website: {startupData.website}</Typography>
                <Typography variant="body1">Locations: {startupData.locations}</Typography>
                <Typography variant="body1">Amount Raised: {startupData.amountRaised}</Typography>
                <Typography variant="body1">Funding Duration: {startupData.fundedOver}</Typography>
              </CardContent>
            </Card>
          ) : (
            <Typography variant="body1">Select a startup to view details.</Typography>
          )}
        </Grid>

        {/* Right Column: All Startups */}
        <Grid item xs={12} md={6}>
          <Typography variant="h5" gutterBottom sx={{ color: 'brown' }}>
            All Startups
          </Typography>
          <Divider sx={{ mb: 2 }} />
          <Grid container spacing={2}>
            {Array.isArray(startupList) && startupList.length > 0 ? (
              startupList.map((startup) => (
                <Grid item xs={12} md={6} key={startup._id}>
                  <Card
                    onClick={() => setSelectedStartupId(startup._id)}
                    sx={{ cursor: "pointer" }}
                  >
                    <CardContent>
                      <Typography variant="h6">{startup.name}</Typography>
                      <Typography variant="body2">{startup.description}</Typography>
                      <Typography variant="body2">Locations: {startup.locations}</Typography>
                    </CardContent>
                  </Card>
                </Grid>
              ))
            ) : (
              <Typography variant="body1">No startups available.</Typography>
            )}
          </Grid>
        </Grid>
      </Grid>

      {/* Form to add new startup */}
      <form onSubmit={handleSubmit}>
        <Typography variant="h6" sx={{ color: 'brown' }}>
          Add New Startup
        </Typography>

        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              name="name"
              label="Name"
              value={formData.name}
              onChange={handleChange}
              required
              sx={{ width: 200 }}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              name="website"
              label="Website"
              value={formData.website}
              onChange={handleChange}
              required
              sx={{ width: 200 }}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              name="description"
              label="Description"
              value={formData.description}
              onChange={handleChange}
              sx={{ width: 200 }}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              name="locations"
              label="Locations"
              value={formData.locations}
              onChange={handleChange}
              sx={{ width: 200 }}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              name="amountRaised"
              label="Amount Raised"
              value={formData.amountRaised}
              onChange={handleChange}
              sx={{ width: 200 }}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              name="funding duration"
              label="Funding duration"
              value={formData.fundedOver}
              onChange={handleChange}
              sx={{ width: 200 }}
            />
          </Grid>
        </Grid>

        <Button variant="contained" type="submit" sx={{ marginTop: 2 }}>
          Add Startup
        </Button>
      </form>

      <Typography>{message}</Typography>
    </Box>
  );
}

export default StartupPage;
